.App {
  text-align: center;
  background-color: #0f1218;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.text-block {
  padding-left: 1vw;
  padding-right: 1vw;
}

.box {
  align-items: center;
  background-color: #ffffff;
  color: #0f1218;
  width: 40vw;
  border-radius: 10px;
  padding-top: 1px;
  padding-bottom: 1px;
  font-size: 2vh;
}

.box-head {
  position: relative;
  top: 2.5vh;
  align-items: center;
  background-color: #ffffff;
  color: #0f1218;
  width: 40vw;
  min-height: 2vh;
  border-radius: 10px;
  font-size: 2.5vh;
}

.App-link {
  color: #25aacf;
}